<template>
  <div class="signup">
    <input type="number" name="stuid" v-model.number="stuid" placeholder="    StudentID">
    <input type="text" name="username" v-model="username" placeholder="    Username">
    <input type="number" name="phone" v-model.number="phoneNumber" placeholder="    PhoneNumber">
    <input type="number" name="group" v-model.number="group" placeholder="    Group 1 or 2">
    <input type="number" name="qq" v-model.number="qq" placeholder="    Qq">
      <input type="password" name="Password" v-model="password" placeholder="    Password">
    <button @click="sendfuc">Sign up</button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      stuid: "",
      username: "",
      phoneNumber: "",
      group: "",
      qq: "",
      password: ""
    };
  },
  methods: {
    sendfuc() {
      if (
        this.stuid == "" ||
        this.username == "" ||
        this.phoneNumber == "" ||
        this.group == "" ||
        this.qq == "" ||
        this.password == ""
      ) {
        alert("请按要求填写信息");
        return false;
      }
      this.axios({
        method: "post",
        baseURL: "http://localhost:8080/",
        url: "user/regist",
        headers: {
          "Content-Type": "application/json"
        },
        data: {
          stu_id: this.stuid,
          name: this.username,
          password: this.password,
          mobile_phone: this.phoneNumber,
          group: this.group,
          QQ: this.qq
        },
        transformRequest: [
          data => {
            return JSON.stringify(data);
          }
        ]
      })
        .then(response => {
          console.table(response);
          if (response.data.state == 1) {
            alert("注册成功");
          } else if (response.data.state == 0) {
            alert("用户已存在或注册失败");
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
