import Vue from 'vue'
import Router from 'vue-router'
import signin from './views/Signin.vue'
import Signup from './views/Signup.vue'
import Reset from './views/Reset.vue'
import Manager from './views/ManageSignin.vue'
Vue.use(Router)

export default new Router({
    base: process.env.BASE_URL,
    routes: [{
            path: '/',
            name: 'signin',
            component: signin
        },
        {
            path: '/signup',
            name: 'signup',
            component: Signup
        },
        // {
        //     path: '/reset',
        //     name: 'reset',
        //     component: Reset
        // }
        {
            path: '/Manager',
            name: 'Manager',
            component: Manager
        }
    ]
});