<template>
<div class="reset">
      <input type="text"  v-model="email" placeholder="    Email">
            <button @click="sendfuc">Reset password</button>
    </div>    
</template>
<script>
export default {
	name: "reset",
	data() {
		return {
			email: "",
		};
	},
	methods: {
		sendfuc() {
			if (this.email == "") {
				alert("请按要求填写信息");
				return false;
			}
			// console.log("email:" + this.email);
			this.axios
				.post("/api/test", this.qs.stringify({ email: this.email }), {
					//   headers: {
					//     'Content-Type': 'application/x-www-form-urlencoded'
					//   }
				})
				.then(
					function(res) {
						//返回数据
						console.log(res.data);
					}.bind(this)
				)
				.catch(
					//错误捕获
					function(err) {
						if (err.response) {
							console.log(err.response);
						}
					}.bind(this)
				);
		},
	},
};
</script>

