<template>
  <div class="signin">
      <input type="text" v-model.number="username" placeholder="    Username">
            <input type="password" v-model="password" placeholder="    Password">
            <button @click="sendfuc">Sign in</button>
  </div>
</template>

<script>
// @ is an alias to /src
export default {
  name: "signin",
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    sendfuc() {
      if (this.username == "" || this.password == "") {
        alert("请按要求填写信息");
        return false;
      }
      if (!/^\d{8}$/.test(this.username)) {
        alert("用户名格式错误");
        return false;
      }
      this.axios({
        method: "post",
        baseURL: "http://localhost:8080/",
        url: "user/login",
        headers: {
          "Content-Type": "application/json"
        },
        data: {
          stu_id: Number(this.username),
          password: this.password
        },
        transformRequest: [
          data => {
            return JSON.stringify(data);
          }
        ]
      })
        .then(response => {
          console.table(response);
          if (response.data.state == 0) {
            alert("密码错误");
          }
          if (response.data.state == 1) {
            alert("登录成功");
          }
          if (response.data.state == -1) {
            alert("用户不存在，请注册");
          }
        })
        .catch(error => {
          console.log(`err:${error}`);
        });
    }
  }
};
</script>
